kground-color: $colorInactiveGrey;\n    color: $colorInactiveDarkGrey;\n    &:active {\n      background-color: $colorInactiveGrey;\n      color: $colorInactiveDarkGrey;\n    }\n    &:hover {\n      box-shadow: none;\n    }\n  }\n  &:active {\n    background-color: $colorPrimaryDark;\n    color: $colorWhite;\n  }\n}\n\n.button-tertiary {\n  border: none;\n  background-color: inherit;\n  color: $colorPrimary;\n  &:hover {\n    text-shadow: 0px 4px 4px $colorBoxShadow;\n  }\n\n  &:disabled {\n    color: $colorInactiveGrey;\n    &:hover {\n      text-shadow: none;\n    }\n    &:active {\n      color: $colorInactiveGrey;\n    }\n  }\n  &:active {\n    color: $colorPrimaryDark;\n  }\n}\n\n.button-tertiary-alpha {\n  border: none;\n  background-color: inherit;\n  color: $colorInactiveDarkGrey;\n  &:hover {\n    text-shadow: 0px 4px 4px $colorBoxShadow;\n  }\n\n  &:disabled {\n    color: $colorInactiveGrey;\n  }\n  &:active {\n    color: $colorPrimaryDark;\n  }\n}\n\n.button-alert {\n  border: 1px solid $colorAlertNegative;\n  background-color: $colorAlertNegative;\n  color: $colorWhite;\n  &:hover {\n    box-shadow: 0px 4px 4px $colorBoxShadow;\n  }\n  &:active {\n    @extend %button-box-shadow;\n    border: 1px solid $colorAlertNegativeDark;\n    background-color: $colorAlertNegativeDark;\n    &:hover {\n      box-shadow: none;\n    }\n  }\n  &:disabled {\n    border: 1px solid $colorInactiveGrey;\n    background-color: $colorInactiveGrey;\n    color: $colorInactiveDarkGrey;\n    &:active,\n    &:focus {\n      background-color: $colorInactiveGrey;\n    }\n    &:hover {\n      box-shadow: none;\n    }\n  }\n}\n"],"sourceRoot":""}]);
